@font-face {
  font-family: "PT Sans", Arial, serif;
  src: local("PT Sans"),
  url('../fonts/ptsans.woff2') format('woff2'),
  url('../fonts/ptsans.woff') format('woff');
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}

@font-face {
  font-family: "PT Sans", Arial, serif;
